          </div>
          <div class="folderPrev-content__item">
            <img src="../../assets/img/game-item.png" alt="">
            <div class="folderPrev-content__item-title">
              <p>Hover Title Multiline Support</p>
            </div>
            <router-link to="/game"></router-link>
          </div>
        </slick>
      </div>
    </div>
  </section>
</template>

<script>

import Slick from 'vue-slick';
import 'slick-carousel/slick/slick.css';
import $ from 'jquery'

export default {
  props: ["title", "slug", "fetchURL", "link"],
  components: {Slick},


  data(){
    return {
      slickArrows: {
        nextArrow: '.next_arrow.' + this.slug + 'Slick',
        prevArrow: '.prev_arrow.' + this.slug + 'Slick'
      },
      folderPrevOptions: {
        slidesToShow: 11,
        slidesToScroll: 2,
        rows: 1,
        infinite: true,
        // centerMode: true,
        variableWidth: true,
        prevArrow: `<button class="prev_arrow ${this.slug}Slick" ref="catPrePrevArrow">
          <svg width="15" height="24" viewBox="0 0 15 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.82 0L0 2.82L9.16 12L0 21.18L2.82 24L14.82 12L2.82 0Z" fill="white"/>
          </svg>
        </button>`,
        nextArrow: `<button class="next_arrow ${this.slug}Slick" ref="catPreNextArrow">
          <svg width="15" height="24" viewBox="0 0 15 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M2.82 0L0 2.82L9.16 12L0 21.18L2.82 24L14.82 12L2.82 0Z" fill="white"/>
          </svg>
        </button>`,
        responsive: [
          {
            breakpoint: 1899,
            settings: {
              variableWidth: true,
              slidesToShow: 10,
              slidesToScroll: 2,
              infinite: true,
              rows: 1
            }
          },
          {
            breakpoint: 576,
            settings: {
              slidesToShow: 5,
              slidesToScroll: 2,
              infinite: true,
              rows: 1
            }
          },
        ]
      }

    }
  },

  methods: {
    folderPrevBeforeChange(event, slick, currentSlide, nextSlide) {
      if (nextSlide == slick.$slides.length-12){
        $(this.slickArrows.nextArrow).hide();
      } else {
        $(this.slickArrows.nextArrow).show();
      }
      if (nextSlide == 0){
        $(this.slickArrows.prevArrow).hide();
      } else {
        $(this.slickArrows.prevArrow).show();
      }
    },
    folderPrevInit() {
      $(this.slickArrows.prevArrow).hide();
    },
    

  },

}
</script>

<style lang="sass" scoped>
.folderPrev
  &-header
    border-radius: 8px 8px 0 0
    background-color: $green
    height: 65px
    display: flex
    align-items: center
    justify-content: space-between
    & h2 
      padding: 0 30px
      margin: 0
      color: $almost_white
      font-family: Montserrat
      font-style: normal
      font-weight: 600
      font-size: 22px
      line-height: 22px
    & a 
      padding: 15px 30px
      color: $dark_green
      font-family: Montserrat
      font-style: normal
      font-weight: bold
      font-size: 18px
      line-height: 22px
      text-decoration: none
  &-content
    width: 100%
    padding: 15px 0
    &__item
      position: relative
      width: 156px !important
      display: flex !important
      border-radius: 8px
      overflow: hidden
      cursor: pointer
      &:hover
        & .folderPrev-content__item-title
          bottom: 0
      & img 
        object-fit: cover
        width: 100%
        height: 100%
      &-title
        position: absolute
        bottom: -90px
        padding: 10px
        background-color: $title_hover
        border-bottom-left-radius: 8px
        border-bottom-right-radius: 8px
        transition: .4s all ease
        & p
          text-align: center
          padding: 0
          margin: 0
          font-family: Montserrat
          font-style: normal
          font-weight: 500
          font-size: 14px
          line-height: 16px
          color: $light_green
      & a 
        position: absolute
        width: 100%
        height: 100%

// @media screen and (max-width: 575.98px)
//   .folderPrev
//     &-content
//       padding-left: 25px

@media screen and (max-width: 575.98px)
  .slider
    margin-left: 0 
    margin-right: 0
  .folderPrev-content__item
    width: 150px !important

@media screen and (max-width: 479.98px)
  .folderPrev
    &-header
      & h2 
        font-size: 18px
        padding: 0px 10px
      & a 
        font-size: 14px
        padding: 0px 10px
</style>